import React, { Fragment, useEffect, useState } from "react";
import "./AIPricing.css";
import AIPlan1 from "../../assets/AIPlan1";
import AIPlan2 from "../../assets/AIPlan2";
import AIPlan3 from "../../assets/AIPlan3";
import AIPlan4 from "../../assets/AIPlan4";
import { axiosPost } from "../../services/axiosService";
import Plans from "../../components/layoutElements/plans";
import AIAnimateFreePlan from "../../assets/AIAnimteFreePlan";
import AIAnimateBasic from "../../assets/AIAnimateBasic";
import AiAnimatestandard from "../../assets/AiAnimatestandard";
import AiAnimateAdvance from "../../assets/AiAnimateAdvance";
import { useDispatch } from "react-redux";
import { userSlice } from "../../redux/User";
import { useLocation } from "react-router-dom";
import { Crisp } from "crisp-sdk-web";

const AIPricing = ({ checked, aiPlan, planType, subscriptionId, isEvent }) => {
  const [buttonClicked, setButtonClicked] = useState(false);
  const location = useLocation();
  const dispatch = useDispatch();
  const queryParam = new URLSearchParams(location.search);
  const [data, setData] = useState([
    {
      image: <AIPlan2 />,
      animate_image: <AIAnimateBasic />,
      title: "Basic",
      desc: "Good superb content for Advanced users  Advanced users",
      price: "25",
      button: "Upgrade Plan",
      button2: "Change Plan",
      features: [
        "Create up to 5 bots",
        "Send up to 3,000 messages per month",
        "Use up to 11M characters for training a bot",
        "Train a bot using up to 3,000 links",
        "Upload multiple files for bot training",
        "Configure Zapier Integration",
        "View Conversation History",
        "Capture Leads Data",
      ],
      loading: false,
      planCode: "basic_01",
      interval_unit: "months",
    },
    {
      image: <AIPlan3 />,
      animate_image: <AiAnimatestandard />,
      title: "Professional",
      desc: "Good superb content for Advanced users  Advanced users",
      price: "95",
      button: "Upgrade Plan",
      button2: "Change Plan",
      features: [
        "Create up to 10 bots",
        "Send up to 30,000 messages per month",
        "Use up to 11M characters for training a bot",
        "Train a bot using up to 5,000 links",
        "Upload multiple files for bot training",
        "Configure Zapier Integration",
        "View Conversation History",
        "Capture Leads Data",
        "Ability to connect your OpenAI API Key",
        "Ability to Remove Chatbot Builder Branding",
      ],
      loading: false,
      planCode: "standard_01",
      interval_unit: "months",
    },
    {
      image: <AIPlan4 />,
      animate_image: <AiAnimateAdvance />,
      title: "Enterprise",
      desc: "Good superb content for Advanced users  Advanced users",
      price: "375",
      button: "Upgrade Plan",
      button2: "Change Plan",
      features: [
        "Personalized SLA",
        "Custom limits",
        "Team training",
        "More messages per month",
        "Unlimited bots",
        "Unlimited links",
      ],
      loading: false,
      planCode: "advanced_01",
      interval_unit: "months",
    },
  ]);

  useEffect(() => {
    const svgs = [
      // "https://dlvkyia8i4zmz.cloudfront.net/laOOO58rTEqLIunp3y8Q_free_plan_pricing.svg",
      "https://dlvkyia8i4zmz.cloudfront.net/xvu3hijDRuqJXkQX0vYS_Basic_plan_pricing.svg",
      "https://dlvkyia8i4zmz.cloudfront.net/DvxuWTfbQkhvfzUhTHmd_Standard_plan_pricing.svg",
      "https://dlvkyia8i4zmz.cloudfront.net/ln7AutUUT6KMHYqlqeuS_Advanced_plan_pricing.svg",
    ];
    const main = document.querySelectorAll(".aiplan-bx");
    const imgBox = document.querySelectorAll(".aiplan-ing-bx");
    const temp: any = [];
    Array.from(imgBox).forEach((ele) => temp.push(ele.innerHTML));
    Array.from(main).forEach((element, index) => {
      element.addEventListener("mouseenter", () => {
        imgBox[
          index
        ].innerHTML = `<object type="image/svg+xml" data=${svgs[index]}><img src=${svgs[index]} alt="" /></object>`;
      });
    });
    const updatedData = data.map((item) => {
      if (checked) {
        return {
          ...item,
          interval_unit: "years",
          planCode: `${item.planCode.slice(0, item.planCode.length - 2)}02`,
        };
      } else {
        return {
          ...item,
          interval_unit: "months",
          planCode: `${item.planCode.slice(0, item.planCode.length - 2)}01`,
        };
      }
    });
    setData(updatedData);
  }, [checked]);

  const getPrice = (title) => {
    const plan = data.find((plan) => plan?.title === title);
    if (plan) {
      if (checked) {
        return parseInt(plan?.price) * (checked ? 10 : 1);
      } else {
        return plan?.price;
      }
    }
    return "";
  };

  const handlePlan = () => {
    Crisp.chat.open();
    Crisp.message.send("text", "We'll get back to you shortly");
  };

  const selectedPlan = (planCode, title, price, type) => {
    const body = {
      plan_code: planCode,
      subscriptionId,
    };
    let unit = aiPlan.plan_code.charAt(aiPlan.plan_code.length - 1);
    if (checked) {
      if (aiPlan.price >= price && unit == type) {
        axiosPost(`/payments/downgrade`, body)
          .then((response) => {
            if (response.data.success) {
              setButtonClicked(false);
              const updatedData = data.map((item) => {
                if (item.title === title) {
                  return { ...item, loading: false };
                }
                return item;
              });
              setData(updatedData);
              dispatch(
                userSlice.actions.setInfoModal({
                  showModal: true,
                  modalType: "success",
                  modalTitle: "Success",
                  modalMessage:
                    "Your plan downgrade has been scheduled, and the changes will only take effect upon renewal.",
                })
              );
            }
          })
          .catch((err) => {
            console.log(err);
            dispatch(
              userSlice.actions.setToastPopUp({
                show: true,
                toastType: "error",
                message: `Something went wrong`,
              })
            );
          })
          .finally(() => {
            setTimeout(() => {
              setButtonClicked(false);
              const updatedData = data.map((item) => {
                if (item.title === title) {
                  return { ...item, loading: false };
                }
                return item;
              });
              setData(updatedData);
            }, 1000);
          });
      } else {
        axiosPost(`/payments/aihostedPage`, body)
          .then((response) => {
            window.location.assign(response.data.url);
          })
          .catch((err) => {
            console.log(err);
            dispatch(
              userSlice.actions.setToastPopUp({
                show: true,
                toastType: "error",
                message: `Something went wrong`,
              })
            );
          })
          .finally(() => {
            setTimeout(() => {
              setButtonClicked(false);
              const updatedData = data.map((item) => {
                if (item.title === title) {
                  return { ...item, loading: false };
                }
                return item;
              });
              setData(updatedData);
            }, 1000);
          });
      }
    } else {
      if (
        aiPlan.price >= price ||
        (unit != type && aiPlan.plan_code !== "trial_02")
      ) {
        axiosPost(`/payments/downgrade`, body)
          .then((response) => {
            if (response.data.success) {
              setButtonClicked(false);
              const updatedData = data.map((item) => {
                if (item.title === title) {
                  return { ...item, loading: false };
                }
                return item;
              });
              setData(updatedData);
              dispatch(
                userSlice.actions.setInfoModal({
                  showModal: true,
                  modalType: "success",
                  modalTitle: "Success",
                  modalMessage:
                    "Your plan downgrade has been scheduled, and the changes will only take effect upon renewal.",
                })
              );
            }
          })
          .catch((err) => {
            console.log(err);
            dispatch(
              userSlice.actions.setToastPopUp({
                show: true,
                toastType: "error",
                message: `Something went wrong`,
              })
            );
          })
          .finally(() => {
            setTimeout(() => {
              setButtonClicked(false);
              const updatedData = data.map((item) => {
                if (item.title === title) {
                  return { ...item, loading: false };
                }
                return item;
              });
              setData(updatedData);
            }, 1000);
          });
      } else {
        axiosPost(`/payments/aihostedPage`, body)
          .then((response) => {
            window.location.assign(response.data.url);
          })
          .catch((err) => {
            console.log(err);
            dispatch(
              userSlice.actions.setToastPopUp({
                show: true,
                toastType: "error",
                message: `Something went wrong`,
              })
            );
          })
          .finally(() => {
            setTimeout(() => {
              setButtonClicked(false);
              const updatedData = data.map((item) => {
                if (item.title === title) {
                  return { ...item, loading: false };
                }
                return item;
              });
              setData(updatedData);
            }, 1000);
          });
      }
    }
  };

  const handleUpgrade = (title, price, interval_unit) => {
    setButtonClicked(true);
    const plan = data.find((plan) => plan?.title === title);
    if (plan) {
      const updatedData = data.map((item) => {
        if (item.title === title) {
          return { ...item, loading: true };
        }
        return item;
      });
      setData(updatedData);
      if (title === "Basic") {
        if (checked) {
          selectedPlan("basic_02", title, 250, "2");
        } else {
          selectedPlan("basic_01", title, 25, "1");
        }
      } else if (title === "Professional") {
        if (checked) {
          selectedPlan("standard_02", title, 950, "2");
        } else {
          selectedPlan("standard_01", title, 95, "1");
        }
      } else if (title === "Enterprise") {
        if (checked) {
          selectedPlan("advanced_02", title, 3750, "2");
        } else {
          selectedPlan("advanced_01", title, 375, "1");
        }
      } else if (title === "Free") {
        selectedPlan("free_01", title, 0, "1");
      }
    }
  };

  const [isHovered, setIsHovered] = useState(false);
  const handleMouseEnter = () => {
    setIsHovered(true);
  };

  const handleMouseLeave = () => {
    setIsHovered(false);
  };

  return (
    <Fragment>
      <div
        className="ai-plan-flex"
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
      >
        {data.map((plan, i) => (
          <div
            className={`aiplan-bx ${
              aiPlan?.plan_code === plan?.planCode ||
              (aiPlan?.plan_code === "trial_01" &&
                plan?.planCode === "basic_01") ||
              (aiPlan?.plan_code === "trial_02" &&
                plan?.planCode === "basic_01")
                ? "active-aiplan"
                : ""
            }`}
            key={i}
          >
            <div className="aiplan-ing-bx">{plan?.image}</div>
            <div className="aiplan-content-bx">
              <h2 className="mb-20">
                {plan?.title}

                {(aiPlan?.trial || aiPlan?.name === "Free") &&
                  plan?.title === "Professional" && (
                    <span className="ai-recommend">Recommended</span>
                  )}

                {(aiPlan?.plan_code === plan?.planCode ||
                  (aiPlan?.plan_code === "trial_01" &&
                    plan?.planCode === "basic_01") ||
                  (aiPlan?.plan_code === "trial_02" &&
                    plan?.planCode === "basic_01")) &&
                  aiPlan?.trial && (
                    <div className="nw-plan-img-bx">
                      <div className="current-pln pln-green pln-position">
                        Trial Plan
                      </div>
                    </div>
                  )}
              </h2>

              <div className="ai-plan-price fx jst-sp-bt">
                {plan.title === "Enterprise" ? (
                  <span>Custom </span>
                ) : (
                  <>
                    <span>${getPrice(plan?.title)}</span> per{" "}
                    {checked ? "year" : "month"}
                  </>
                )}
              </div>

              {((checked &&
                plan?.title === aiPlan?.name?.trim() &&
                (planType === "years" || plan?.title === "Free") &&
                !aiPlan?.trial) ||
                (!checked &&
                  plan?.title === aiPlan?.name?.trim() &&
                  planType === "months" &&
                  !aiPlan?.trial)) && (
                <button
                  disabled={
                    plan?.loading ||
                    (plan?.planCode === aiPlan?.plan_code && !aiPlan?.trial) ||
                    plan?.title?.trim() == "Free" ||
                    buttonClicked ||
                    isEvent
                  }
                >
                  Current Plan
                </button>
              )}

              {!(
                !checked &&
                plan?.title === aiPlan?.name?.trim() &&
                (planType === "months" || plan?.title === "Free") &&
                !aiPlan?.trial
              ) &&
                !(
                  checked &&
                  plan?.title === aiPlan?.name?.trim() &&
                  (planType === "years" || plan?.title === "Free") &&
                  !aiPlan?.trial
                ) &&
                plan.title !== "Enterprise" && (
                  <Fragment>
                    {checked ? (
                      <button
                        onClick={() =>
                          handleUpgrade(
                            plan?.title,
                            plan?.price,
                            plan?.interval_unit
                          )
                        }
                        disabled={
                          plan?.loading ||
                          (plan?.planCode === aiPlan?.plan_code &&
                            !aiPlan?.trial) ||
                          plan?.title?.trim() == "Free" ||
                          buttonClicked ||
                          isEvent
                        }
                      >
                        {getPrice(plan?.title) < aiPlan?.price &&
                        plan.interval_unit === planType ? (
                          <>{plan?.loading ? "Changing..." : plan?.button2}</>
                        ) : (
                          <>{plan?.loading ? "Upgrading..." : plan?.button}</>
                        )}
                      </button>
                    ) : (
                      <button
                        onClick={() =>
                          handleUpgrade(
                            plan?.title,
                            plan?.price,
                            plan?.interval_unit
                          )
                        }
                        disabled={
                          plan?.loading ||
                          (plan?.planCode === aiPlan?.plan_code &&
                            !aiPlan?.trial) ||
                          plan?.title?.trim() == "Free" ||
                          buttonClicked ||
                          isEvent
                        }
                      >
                        {getPrice(plan?.title) < aiPlan?.price ? (
                          <>{plan?.loading ? "Changing..." : plan?.button2}</>
                        ) : (
                          <>{plan?.loading ? "Upgrading..." : plan?.button}</>
                        )}
                      </button>
                    )}
                  </Fragment>
                )}
              {plan.title === "Enterprise" && (
                <button onClick={handlePlan}>Contact us</button>
              )}

              <ul>
                {plan?.title === "Enterprise" ? (
                  <span>Looking For: </span>
                ) : (
                  <span>Features</span>
                )}
                {/* <span>Features</span> */}
                {plan?.features.map((feature: any, index) => (
                  <li key={index}>
                    <span> {feature}</span>
                  </li>
                ))}
              </ul>
            </div>
          </div>
        ))}
      </div>
    </Fragment>
  );
};

export default AIPricing;
