import { Fragment, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useLocation, useNavigate } from "react-router-dom";
import { userSlice } from "../../redux/User";
import { State } from "../../redux/Types";
import { axiosPost } from "../../services/axiosService";
import { setCookie } from "../../services/cookieService";
import "../../assets/plansimage/FreePlan";
import PlusPlan from "../../assets/plansimage/PlusPlan";
import CustomPlan from "../../assets/plansimage/CustomPlan";
import SmallLoader from "../loaders/SmallLoader";
import { io } from "socket.io-client";
import AIPricing from "../../pages/NewPricing/AIPricing";
import environment from "./../../constants/Environment";
import Coupon from "../coupon";
import * as GtmService from "../../services/gtmService";
import useGetCompany from "@/hooks/useGetCompany";
import { Crisp } from "crisp-sdk-web";
import { motion } from "framer-motion";
import { useQueryClient } from "@tanstack/react-query";
import PaymentSummary from "./PaymentSummary";
import SettingsNavbar from "./HorizontalNavbars/SettingsNavbar";
import { PlanModals } from "@/pages/agent/PlanAddonModal";
const environmentData = environment();

interface Props {
  calledFromAgent?: any;
  showPlansModal?: string;
  setShowPlansModal?: React.Dispatch<React.SetStateAction<PlanModals>>;
}

const Plans = ({ calledFromAgent, showPlansModal = "", setShowPlansModal = () => {} } : Props) => {
  const dispatch = useDispatch();
  const router = useNavigate();
  const location = useLocation();
  const queryParam = new URLSearchParams(location.search).get("hostedpage_id");
  const upgraded = new URLSearchParams(location.search).get("checked");
  const priceCode = new URLSearchParams(location.search).get('code')
  const priceVal = new URLSearchParams(location.search).get('price')
  const add = new URLSearchParams(location.search).get('addon')
  const [checked, setChecked] = useState<any>( (upgraded == 'true' || calledFromAgent) ? true : false);
  const userEmail = useSelector((state: State) => state.user.userEmail);
  const compDetails = useSelector((state: State) => state.user.companyDetails);
  const websocketPrice = useSelector((state: State) => state.user.socket);
  const [plan, setPlan] = useState<any>({});
  const [planType, setPlanType] = useState<any>("");
  const [aiPlan, setAiPlan] = useState<any>({});
  const [loading, setLoading] = useState<boolean>(false);
  const [reloadState, setReloadState] = useState<boolean>(true);
  const [subscriptionId, setSubscriptionId] = useState<any>("");
  const [subscription, setSubscription] = useState<any>({});
  const [subId, setSubId] = useState<any>("");
  const [isEvent, setIsEvent] = useState(false);
  const [checkOutPage, setCheckOutPage] = useState(priceCode ? true : false);
  // const showCoupon = useSelector((state: State) => state.user.showCoupon)
  const [isApplied, setIsApplied] = useState<boolean>(false);
  const [showCoupon, setShowCoupon] = useState<boolean>(false);
  const [code, setCode] = useState<string>("");
  const [price, setPrice] = useState(Number(priceVal) ?? 0);
  const queryClient = useQueryClient();
  const [addons, setAddons] = useState(add)
  const [customerId, setCustomerId] = useState("");
  const [planCode, setPlaneCode] = useState(priceCode ?? "");
  const companyDetails = useGetCompany();

  useEffect(() => {
    if (companyDetails.isSuccess) {
      const response = companyDetails.data;
      if (response && response.data.token) {
        setCookie(
          "token",
          response.data.token,
          1,
          environment()?.PUBLIC_DOMAIN,
          "/",
        );
        setCookie(
          "token",
          response.data.token,
          1,
          environment()?.PAGE_DOMAIN,
          "/",
        );
      }
      const units =
        !environment().isAI && response.data.data.subscription?.interval_unit;

      const plan = environment().isAI
        ? response.data.data.ai_subscription?.plan
        : response.data.data.subscription?.plan;
      const billing = environment().isAI
        ? response.data.data.ai_billing?.current_term_ends_at
        : response.data.data.billing?.current_term_ends_at;

      const customer = environment().isAI
        ? response.data.data?.ai_subscription?.customer?.customer_id
        : response.data.data?.subscription?.customer?.customer_id;

      setCustomerId(customer);
      setSubscription(response.data.data);

      // if (!plan?.trial) {
      //   const currentDate = new Date().getTime();
      //   const trialEndDate = new Date(billing?.current_term_ends_at).getTime();
      //   if (currentDate <= trialEndDate) {
      //     router("/dashboard");
      //   }
      // }
      // setBilling(billing);
      setPlan(response?.data?.data?.subscription?.plan ?? {});
      setAiPlan(response?.data?.data?.ai_subscription?.plan ?? {});
      setSubscriptionId(response?.data?.data?.ai_subscription?.subscription_id);
      setSubId(response?.data?.data?.subscription?.subscription_id);

      setPlanType(response.data?.data?.ai_subscription?.interval_unit);

      const checkCode = environment().isAI
        ? response?.data?.data?.ai_subscription?.plan?.plan_code
        : response?.data?.data?.subscription?.plan?.plan_code;
      // if ((upgraded == "true")) setChecked(true);
      console.log('qwewffd',calledFromAgent, upgraded)
      if (
        checkCode === "standard_02" ||
        checkCode === "basic_02" ||
        checkCode === "advanced_02" ||
        checkCode === environmentData.YEARLYPLAN ||
        units === "year" 
    
      ) {
        setChecked(true);
      } 
      setReloadState(false);
    }
  }, [companyDetails.data]);

  useEffect(() => {
    const socket = io(environmentData.SERVER_API_URL, {
      reconnection: false,
      transports: ["websocket"],
    });

    socket.on("connect", () => {
      console.log("socket conneted");
      dispatch(userSlice.actions.setSocket(true));
    });

    socket.emit("price", { userEmail: userEmail });
    socket.on("event", (res) => {
      const companyName: any = compDetails.compName;
      const userId: any = compDetails.userId;
      const compId: any = compDetails._id;
      console.log(
        "sockettt:::",
        res.data?.previous_attributes?.items?.data[0].plan.id,
        res.data?.object?.items?.data[0].plan?.id,
      );

      console.log("socket data : ", res.data);
      queryClient.invalidateQueries({
        queryKey: ["getCompany"],
      });
      if (
        res.data?.previous_attributes?.items?.data[0].plan.id ===
        res.data?.object?.items?.data[0].plan?.id
      ) {
        socket.off("connect");
        socket.disconnect();
        dispatch(userSlice.actions.setSocket(false));
        setIsEvent(false);

        return;
      }
      if (typeof window.gr === "function") {
        window.gr("track", "conversion", {
          email: userEmail,
        });
      }
      GtmService.pushToDataLayer({
        event:
          res.data?.previous_attributes?.items?.data[0].plan.id ===
          environmentData.TRIALPLAN
            ? "purchase"
            : "upgrade",
        user_email: userEmail ?? "",
        plan_name: res.data?.object?.items?.data[0].plan?.nickname ?? "",
        plan_code: res.data?.object?.items?.data[0].plan?.id ?? "",
        customer_id: res.data?.object?.customer ?? "",
        subscription_id: res.data?.object?.id ?? "",
        company_name: companyName ?? "",
        company_id: compId ?? "",
        user_id: userId ?? "",
      });

      if (
        // res.data?.plan?.plan_code === "basic_01" ||
        // res.data?.plan?.plan_code === "basic_02" ||
        // res.data?.plan?.plan_code === "standard_01" ||
        // res.data?.plan?.plan_code === "standard_02" ||
        res.data?.object?.items?.data[0].plan?.id ===
          environmentData.MONTHLYPLAN ||
        res.data?.object?.items?.data[0].plan?.id ===
          environmentData.YEARLYPLAN
      ) {
        dispatch(
          userSlice.actions.setToastPopUp({
            show: true,
            toastType: "success",
            message: `Your plan upgraded successfully`,
          }),
        );
      }
      setIsEvent(false);
      dispatch(userSlice.actions.setSocket(false));
      socket.off("connect");
      socket.disconnect();
    });
  }, []);

  useEffect(() => {
    if (!calledFromAgent) {
      if (queryParam && queryParam.trim().length >= 1) {
        setIsEvent(true);
      }
      router("/settings");
    }
    calledFromAgent && setShowPlansModal("plan");
  }, [])

  const checkSaveMethod = async (planCode, price) => {
    setPlaneCode(planCode);
    setLoading(true);
    setPrice(price);
    setIsEvent(true);
 
 
    try {
      if (plan?.price > price) {
        // for downgrade
        const data = {
          subId,
          plan_code: planCode,
        };
        const downgrade = await axiosPost("/payments/omniDowngrade", data);
        dispatch(
          userSlice.actions.setInfoModal({
            showModal: true,
            modalType: "success",
            modalTitle: "Your plan downgraded successfully",
            modalMessage:
              "The downgrade changes made will be reflected in your account from next billing.",
            modalButtonText: "Go To Billing",
          }),
        );
      } else {
        const data = {
          customerId,
        };
        setCheckOutPage(true);
        // if (plan.trial) {
        //   setCheckOutPage(true);
        // } else {
        //   const data = {
        //     subscriptionId,
        //     customerId,
        //     planCode,
        //   };
        //   const res = await axiosPost("/payments/upgradePlan", data);
        //     if(res.data.message === 'your plan will be updated shortly'){
        //       dispatch(
        //         userSlice.actions.setToastPopUp({
        //           show: true,
        //           toastType: "success",
        //           message: res.data.message,
        //         }),
        //       );

        //     }else {
        //       dispatch(
        //         userSlice.actions.setInfoModal({
        //           showModal: true,
        //           modalType: "success",
        //           modalTitle: "Plan Schedule Successfully",
        //           modalMessage: res.data.message,
        //           modalButtonText: "Go To Billing"
        //         })
        //       );
        //     }

        // }
      }
    } catch (err) {
    } finally {
      setLoading(false);
    }
  };


  const handlePlan = async () => {
    Crisp.chat.open();
    Crisp.message.send(
      "text",
      "I'm looking for an enterprise plan quote. Would you be able to help?",
    );
    await axiosPost(`/internal/email`, {
      userEmail,
      current_plan: plan.name,
    });
  };

  return (
    <Fragment>
      {(calledFromAgent ? showPlansModal === "plan" : 1) && (<motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
        transition={{ duration: 1 }}
        className="api-container transition-group settings-container"
      >
       
        {!calledFromAgent && <SettingsNavbar title={"Plans and Pricing"} />}
        {reloadState ? (
          <SmallLoader />
        ) : (
          <>
            {environment().isAI ? (
              <Fragment>
                <div className="pricing-head mt-84">
                  <h1>Choose the best plan for your business </h1>

                  <div className="pricing-toggle">
                    <span
                      className={
                        checked ? "monthly-label" : "monthly-label selected"
                      }
                    >
                      Monthly
                    </span>
                    <label
                      className="pricing-toggle-wrapper"
                      onChange={() => {
                        setChecked(!checked);
                      }}
                    >
                      <input type="checkbox" checked={checked} />
                      <span className="toggle-switch round"></span>
                    </label>
                    <span
                      className={
                        checked ? "yearly-label selected" : "yearly-label"
                      }
                    >
                      Yearly
                    </span>
                  </div>
                  <p>(Save 2 months with annual billing)</p>
                </div>
                <AIPricing
                  checked={checked}
                  aiPlan={aiPlan}
                  planType={planType}
                  subscriptionId={subscriptionId}
                  isEvent={isEvent}
                />
                {(aiPlan.plan_code === "trial_01" ||
                  aiPlan.plan_code === "trial_02") && (
                  <>
                    <div
                      className={`coupon-container fx al-fx-cnt jstfy-sp-btw mt-30 gap-20 ${
                        isApplied ? "coupon-applied" : ""
                      }`}
                    >
                      <div className="fx al-fx-cnt gap-10">
                        {isApplied ? (
                          <Fragment>
                            <svg
                              width="40"
                              height="40"
                              viewBox="0 0 40 40"
                              fill="none"
                              xmlns="http://www.w3.org/2000/svg"
                            >
                              <path
                                d="M40 20C40 31.0457 31.0457 40 20 40C8.9543 40 0 31.0457 0 20C0 8.9543 8.9543 0 20 0C31.0457 0 40 8.9543 40 20Z"
                                fill="#4CAF50"
                              />
                              <path
                                d="M12 18.9444L17.7778 24.7222L28 14.5"
                                stroke="white"
                                strokeWidth="2"
                              />
                            </svg>
                            <div className="coupon-details">
                              <h3>Coupon/Promo Code</h3>
                              <p>{code} code is successfull applied</p>
                            </div>
                          </Fragment>
                        ) : (
                          <Fragment>
                            <svg
                              width="48"
                              height="48"
                              viewBox="0 0 48 48"
                              fill="none"
                              xmlns="http://www.w3.org/2000/svg"
                            >
                              <rect
                                y="0.000976562"
                                width="48"
                                height="47.999"
                                rx="23.9995"
                                fill="#FB5F66"
                              />
                              <path
                                d="M17.6924 19.9941C17.5512 19.9941 17.4158 20.0502 17.316 20.15C17.2162 20.2498 17.1602 20.3852 17.1602 20.5263V21.6823C17.1602 21.8234 17.2162 21.9588 17.316 22.0586C17.4158 22.1584 17.5512 22.2145 17.6924 22.2145C17.8335 22.2145 17.9689 22.1584 18.0687 22.0586C18.1685 21.9588 18.2246 21.8234 18.2246 21.6823V20.5263C18.2246 20.3852 18.1685 20.2498 18.0687 20.15C17.9689 20.0502 17.8335 19.9941 17.6924 19.9941ZM17.6924 22.8851C17.5512 22.8851 17.4158 22.9411 17.316 23.0409C17.2162 23.1408 17.1602 23.2761 17.1602 23.4173V24.5732C17.1602 24.7144 17.2162 24.8497 17.316 24.9495C17.4158 25.0493 17.5512 25.1054 17.6924 25.1054C17.8335 25.1054 17.9689 25.0493 18.0687 24.9495C18.1685 24.8497 18.2246 24.7144 18.2246 24.5732V23.4173C18.2246 23.2761 18.1685 23.1408 18.0687 23.0409C17.9689 22.9411 17.8335 22.8851 17.6924 22.8851ZM17.6924 25.776C17.5512 25.776 17.4158 25.8321 17.316 25.9319C17.2162 26.0317 17.1602 26.1671 17.1602 26.3082V27.4641C17.1602 27.6053 17.2162 27.7407 17.316 27.8405C17.4158 27.9403 17.5512 27.9963 17.6924 27.9963C17.8335 27.9963 17.9689 27.9403 18.0687 27.8405C18.1685 27.7407 18.2246 27.6053 18.2246 27.4641V26.3082C18.2246 26.1671 18.1685 26.0317 18.0687 25.9319C17.9689 25.8321 17.8335 25.776 17.6924 25.776Z"
                                fill="white"
                              />
                              <path
                                d="M16.0258 13.8877C15.713 13.9135 15.4147 14.0303 15.1675 14.2237C14.9204 14.4171 14.7353 14.6787 14.6351 14.9761L13.9262 17.116C13.0619 17.1884 12.3711 17.9143 12.3711 18.7957V20.9133C12.3712 21.0258 12.4069 21.1354 12.4732 21.2264C12.5394 21.3173 12.6328 21.3849 12.7399 21.4194C13.4041 21.6339 13.9677 22.4679 13.9677 23.4892C13.9677 24.511 13.4041 25.3455 12.7399 25.5594C12.6327 25.594 12.5393 25.6617 12.473 25.7527C12.4067 25.8438 12.3711 25.9535 12.3711 26.0661V29.2465C12.3732 29.695 12.5523 30.1246 12.8694 30.4417C13.1866 30.7588 13.6161 30.9379 14.0646 30.94H22.4667L31.5026 34.0252C32.3371 34.3099 33.2499 33.8437 33.525 33.0124L34.2148 30.9272C35.0876 30.8644 35.7885 30.1343 35.7885 29.2465V26.065C35.7884 25.9525 35.7527 25.8429 35.6864 25.752C35.6202 25.661 35.5268 25.5935 35.4197 25.5589C34.7555 25.3444 34.1919 24.5105 34.1919 23.4886C34.1919 22.4673 34.7555 21.6328 35.4197 21.4184C35.5268 21.3838 35.6202 21.3162 35.6864 21.2253C35.7527 21.1344 35.7884 21.0248 35.7885 20.9122V18.7957C35.7863 18.3473 35.6071 17.9179 35.29 17.6009C34.9729 17.2839 34.5434 17.1048 34.095 17.1027H25.6951L16.6607 13.9633C16.4516 13.8904 16.2233 13.8994 16.0263 13.8877H16.0258ZM16.104 14.9436C16.1732 14.9388 16.2451 14.9478 16.3169 14.9718L22.4502 17.1027H15.0524L15.6453 15.3092C15.6758 15.209 15.736 15.1204 15.8179 15.0552C15.8998 14.9899 15.9996 14.951 16.104 14.9436ZM14.0646 18.1671H17.161V18.6733C17.161 18.8144 17.2171 18.9498 17.3169 19.0496C17.4167 19.1494 17.5521 19.2055 17.6932 19.2055C17.8344 19.2055 17.9698 19.1494 18.0696 19.0496C18.1694 18.9498 18.2254 18.8144 18.2254 18.6733V18.1671H34.095C34.4527 18.1671 34.7241 18.4386 34.7241 18.7957V20.6078C33.7411 21.1257 33.1274 22.2417 33.1274 23.4892C33.1274 24.7361 33.7416 25.8522 34.7241 26.3695V29.2465C34.7241 29.6042 34.4527 29.8756 34.095 29.8756H18.2244C18.227 29.692 18.2254 29.5031 18.2254 29.3173C18.2254 29.1762 18.1694 29.0408 18.0696 28.941C17.9698 28.8412 17.8344 28.7851 17.6932 28.7851C17.5521 28.7851 17.4167 28.8412 17.3169 28.941C17.2171 29.0408 17.161 29.1762 17.161 29.3173C17.16 29.502 17.161 29.6931 17.1621 29.8756H14.0646C13.7064 29.8756 13.4355 29.6042 13.4355 29.2465V26.3705C14.4185 25.8527 15.0322 24.7367 15.0322 23.4892C15.0322 22.2417 14.418 21.1262 13.4355 20.6089V18.7957C13.4355 18.438 13.707 18.1671 14.0646 18.1671ZM25.7605 30.94H33.0891L32.5143 32.6777C32.4185 32.9666 32.1285 33.1141 31.8469 33.0183L25.7605 30.94Z"
                                fill="white"
                              />
                              <path
                                d="M28.7451 24.8289C29.6924 24.8289 30.4716 25.6075 30.4716 26.5548C30.4716 27.5022 29.6924 28.2824 28.7451 28.2824C27.7977 28.2824 27.0175 27.5022 27.0175 26.5548C27.0175 25.6075 27.7977 24.8289 28.7451 24.8289ZM28.7451 25.8933C28.3725 25.8933 28.0819 26.1828 28.0819 26.5548C28.0819 26.9269 28.373 27.218 28.7451 27.218C29.1171 27.218 29.4071 26.9274 29.4071 26.5548C29.4071 26.1823 29.1171 25.8933 28.7451 25.8933ZM23.6773 19.7607C24.1345 19.763 24.5724 19.9456 24.8957 20.2689C25.219 20.5922 25.4016 21.03 25.4038 21.4872C25.4038 22.4345 24.6247 23.2137 23.6768 23.2137C22.7289 23.2137 21.9492 22.4345 21.9492 21.4872C21.9515 21.0298 22.1342 20.5918 22.4578 20.2685C22.7813 19.9452 23.2199 19.7627 23.6773 19.7607ZM23.6773 20.8251C23.3048 20.8251 23.0136 21.1152 23.0136 21.4872C23.0136 21.8598 23.3048 22.1493 23.6768 22.1493C24.0493 22.1493 24.3394 21.8592 24.3394 21.4872C24.3394 21.1147 24.0499 20.8251 23.6773 20.8251ZM28.3688 21.1109L23.3 26.1796C23.203 26.28 23.1494 26.4145 23.1506 26.554C23.1518 26.6935 23.2078 26.827 23.3065 26.9257C23.4051 27.0244 23.5386 27.0803 23.6782 27.0816C23.8177 27.0828 23.9522 27.0291 24.0525 26.9322L29.1213 21.8635C29.2211 21.7637 29.2772 21.6283 29.2772 21.4872C29.2772 21.3461 29.2211 21.2107 29.1213 21.1109C28.9037 20.9178 28.5732 20.8975 28.3688 21.1109Z"
                                fill="white"
                              />
                            </svg>
                            <div className="coupon-details">
                              <h3>Coupon/Promo Code</h3>
                              {/* <p>Apply your coupon code to receive a discount on our premium plan.</p> */}
                            </div>
                          </Fragment>
                        )}
                      </div>
                      {isApplied ? (
                        <a className="cp">Applied</a>
                      ) : (
                        <a className="cp" onClick={() => setShowCoupon(true)}>
                          Apply Code
                        </a>
                      )}
                    </div>
                    {showCoupon && (
                      <Coupon
                        setIsApplied={setIsApplied}
                        setShowCoupon={setShowCoupon}
                        setCode={setCode}
                      />
                    )}
                  </>
                )}
              </Fragment>
            ) : (
              <Fragment>
                <div className="settings_wrapper mt-84">
                  <div className="pricing-head pb-30">
                    <h1>Choose the best plan for your business </h1>

                    <div className="pricing-toggle">
                      <span
                        className={
                          checked ? "monthly-label" : "monthly-label selected"
                        }
                      >
                        Monthly
                      </span>
                      <label
                        className="pricing-toggle-wrapper"
                        onChange={() => {
                          setChecked(!checked);
                        }}
                      >
                        <input type="checkbox" checked={checked} />
                        <span className="toggle-switch round"></span>
                      </label>
                      <span
                        className={
                          checked ? "yearly-label selected" : "yearly-label"
                        }
                      >
                        Yearly
                      </span>
                    </div>
                    <p>(Save 2 months with annual billing)</p>
                  </div>
                  <div className="plan-nw no-p gap-30">
                    <div
                      className={`plus-plan-nw-bx ${
                        (checked &&
                          (plan?.plan_code ===
                            environmentData.YEARLYPLAN ||
                            plan?.plan_code === "lifetime_02")) ||
                        (!checked &&
                          (plan?.plan_code ===
                            environmentData.MONTHLYPLAN ||
                            plan?.plan_code === "lifetime_01" ||
                            plan?.plan_code ===
                              environmentData.TRIALPLAN ||
                            plan?.plan_code === "trial_02"))
                          ? "planactive"
                          : ""
                      } `}
                    >
                      <div className="nw-plan-img-bx">
                        {((checked &&
                          plan?.plan_code ===
                            environmentData.YEARLYPLAN) ||
                          (!checked &&
                            (plan?.plan_code ===
                              environmentData.MONTHLYPLAN ||
                              plan?.plan_code ===
                                environmentData.TRIALPLAN ||
                              plan?.plan_code === "trial_02"))) && (
                          <div className={`current-pln pln-green`}>
                            {plan?.trial ? "Trial Plan" : "Current Plan"}
                          </div>
                        )}
                        <PlusPlan />
                      </div>

                      <div className="nw-plan-head">
                        <div className="hd-rght">
                          <h1>
                            {" "}
                            {plan?.name === "Omniengage Lifetime" &&
                              "Lifetime"}{" "}
                            Plus plan{" "}
                          </h1>
                          <p className="no-m">Scale up with ease</p>
                        </div>
                        <div>
                          <hr />
                        </div>
                        <div className="hd-lft">
                          <div className="plan-tag">
                            <div className="starts-at">Starts at</div>
                            <div className="price-plan">
                            <span>
                                {plan?.price === 220 ? (
                                  <>
                                    <sup></sup>${checked ? 220 : 22}
                                  </>
                                ) : (
                                  <>
                                    <sup></sup>{checked ? <><del>$220</del> $187</> : <>$22</>}
                                  </>
                                )}
                              </span>
                              <span className="nw-plan-time">
                                /{checked ? "year" : "month"}
                              </span>
                            </div>
                          </div>
                        </div>
                      </div>

                      <div
                        className="nw-panel"
                        style={{ position: "relative" }}
                      >
                        <button
                          className="mt-20"
                          onClick={() => {
                            // checkSaveMethod(plan?.plan_code, plan?.price)
                            calledFromAgent && setShowPlansModal("summary");
                            checked
                              ? checkSaveMethod(
                                  environmentData.YEARLYPLAN,
                                  220,
                                )
                              : checkSaveMethod(
                                  environmentData.MONTHLYPLAN,
                                  22,
                                );
                          }}
                          disabled={
                            (!plan?.trial &&
                              plan?.plan_code ===
                                environmentData.YEARLYPLAN &&
                              checked) ||
                            (!plan?.trial &&
                              plan?.plan_code ===
                                environmentData.MONTHLYPLAN &&
                              !checked) ||
                            isEvent ||
                            loading
                          }
                        >
                          {loading ? "Upgrading..." : "Upgrade Now"}
                        </button>
                        {/* <button className="customize-plan-btn" onClick={handlePlan}>
                    Customize Plan
                  </button> */}
                        <i className="mb-20">
                          Min. Bill amount {checked ? "$220/year" : "$22/month"}
                        </i>
                      </div>

                      <ul className="nw-plan-list">
                        <li>
                          <span>3 Workflows</span>
                        </li>
                        <li>
                          <span>{checked ? "3600" : "300"} AI Credits</span>
                        </li>
                        <li>
                          <span> {checked ? "6,000" : "500"} Emails</span>
                        </li>
                        {/* <li>
                <span>{checked ? "10" : "10"} Audience segments</span>
              </li>
               <li>
                <span>{checked ? "15" : "15"} Data tags</span>
              </li> */}
                        <li>
                          <span> A/B testing</span>
                        </li>
                        <li>
                          <span> Email Warmup</span>
                        </li>
                      </ul>
                      {/* <div className="add-onBar"> AddOns</div>
                      <ul className="nw-plan-list plus-plan-list">
                        <li>
                          <span>
                            {checked
                              ? "$30 for every 12,000 extra yearly"
                              : "$3 for every 1000 extra monthly"}{" "}
                            push web notifications
                          </span>
                        </li>
                        <li>
                          <span>
                            {checked
                              ? "$30 for every 12,000 extra yearly"
                              : "$3 for every 1000 extra monthly"}{" "}
                            emails
                          </span>
                        </li>
                        {showViewMore && (
                          <>
                            <li>
                              <span>
                                {" "}
                                Omniengage Branding removal:{" "}
                                {checked ? "$750 per year" : "$75 per month"}
                              </span>
                            </li>
                            <li>
                              <span>
                                {" "}
                                Extra user for{" "}
                                {checked ? "$120 per year" : "$12 per month"}
                              </span>
                            </li>
                            <li>
                              <span>
                                {checked
                                  ? "$90 for every 12,000 extra yearly"
                                  : "$9 for every 1000 extra monthly"}{" "}
                                SMS
                              </span>
                            </li>
                          </>
                        )}
                      </ul>
                      <div
                        className="view-more"
                        onClick={(e) => {
                          setShowViewMore((prev) => !prev);
                        }}
                      >
                        View {showViewMore ? "Less" : "More"}
                      </div> */}
                    </div>
                    <div className="plan-nw-bx plan-nwcustom">
                      <div className="nw-plan-img-bx">
                        {/* {(plan.name === "Plus" || plan.name === "Lifetime") && <div className="current-pln pln-yellow">Current Plan</div>} */}
                        <CustomPlan />
                      </div>
                      <div className="nw-plan-head">
                        <div className="hd-rght">
                          <h1>Enterprise plan</h1>
                          <p className="no-m">Unleash your full potential</p>
                        </div>
                        <hr />
                        <div className="hd-lft">
                          <span>Custom</span>
                        </div>
                      </div>
                      <div className="nw-panel">
                        <button onClick={handlePlan}>Contact us</button>
                      </div>
                      <ul className="nw-plan-list">
                        <li>
                          <span>
                            {" "}
                            Perfect for businesses with high volume requirements
                          </span>
                        </li>
                        {/* <li>
                          <span>
                            {" "}
                            Take advantage of special rates and customized plans
                          </span>
                        </li> */}
                        <li>
                          <span> Over 20 workflows</span>
                        </li>
                        <li>
                          <span> Access to all features</span>
                        </li>
                        <li>
                          <span> Priority support</span>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
              </Fragment>
            )}
          </>
        )}
      </motion.div>)}
      {checkOutPage && (
        <PaymentSummary
          subscription={subscription}
          setCheckOutPage={setCheckOutPage}
          planCode={planCode}
          setIsEvent={setIsEvent}
          setLoading={setLoading}
          isExpired={false}
          price={price}
          addon={addons}
          calledFromAgent={calledFromAgent}
          setShowPlansModal={setShowPlansModal}
        />
      )}
    </Fragment>
  );
};
export default Plans;
