interface PROPS {
  downgradeReason: string;
  handleDowngradeReason(value: string): void;
}

function DowngradeStep3(props: any) {
  const { downgradeReason, handleDowngradeReason }: PROPS = props;

  return (
    <div className="downgrade-body fx al-cnt fx-d-col fx-strt step-1 gap-30 step-3">
      <div className="downgrade-body-description step-3 flex-cnt fx-d-col">
        <div className="downgrade-description-title">Why would you like to downgrade your  Add-Ons?</div>
        <div className="downgrade-description-subtitle">We want to understand your needs better</div>
      </div>
      <div className="downgrade-body-content step-3 new-scrollbar">
        <textarea placeholder="Type here..." value={downgradeReason} onChange={(event) => handleDowngradeReason(event.target.value)} />
      </div>
    </div>
  );
}

export default DowngradeStep3;
