import { AddOn, ADDON_ICON } from "../manageAddons";
import { Billing } from "./Downgrade";

interface PROPS {
  addonData: AddOn[];
  billingData: Record<string, Billing>;
  handleAddonSelect(addonKey: string): void;
}

function DowngradeStep1(props: any) {
  const { addonData, billingData, handleAddonSelect }: PROPS = props;

  return (
    <div className="downgrade-body fx al-cnt fx-d-col fx-strt step-1 gap-30">
      <div className="downgrade-body-description step-1">Kindly choose the  Add-Ons that you want to downgrade</div>
      <div className="downgrade-body-content step-1 gap-20 wd-100">
        {addonData.map((addon: AddOn) => (
          addon.key !== "audienceSegments" && addon.key !== "dataTags" && (
            <div
              key={addon.key}
              className={
                billingData[addon.key]?.quantity
                  ? billingData[addon.key]?.selected
                    ? "downgrade-addon fx selected"
                    : "downgrade-addon fx"
                  : "downgrade-addon fx disabled"
              }
              onClick={() => billingData[addon.key].quantity && handleAddonSelect(addon.key)}
            >
              <div className="downgrade-addon-icon flex-cnt">
                <span className="flex-cnt">{ADDON_ICON[addon.key]}</span>
              </div>
              <div className="downgrade-addon-body fx jstfy-sp-btw wd-100">
                <div className="downgrade-addon-title">{addon.name}</div>
                <div className="downgrade-addon-check">
                  <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <rect width="20" height="20" rx="10" fill="#FB5F66" />
                    <path
                      d="M8.22883 13.6095C7.94497 13.6096 7.67274 13.4968 7.47219 13.2959L5.18461 11.0092C4.93846 10.7629 4.93846 10.3638 5.18461 10.1176C5.43083 9.87143 5.82996 9.87143 6.07618 10.1176L8.22883 12.2702L13.9238 6.57523C14.17 6.32909 14.5692 6.32909 14.8154 6.57523C15.0615 6.82146 15.0615 7.22059 14.8154 7.46681L8.98547 13.2959C8.78492 13.4968 8.51269 13.6096 8.22883 13.6095Z"
                      fill="white"
                    />
                  </svg>
                </div>
              </div>
            </div>
          )
        ))}
      </div>
    </div>
  );
}

export default DowngradeStep1;
