import { Fragment } from "react";
import { AddOn, ADDON_ICON } from "../manageAddons";
import { Billing, Total } from "./Downgrade";
import moment from "moment";

interface PROPS {
  addonData: AddOn[];
  nextTerm:any;
  billingData: Record<string, Billing>;
  totalData: Total;
  planInterval: "monthly" | "yearly";
  handleQuantity(addonKey: string, action: "increase" | "decrease"): void;
}

function DowngradeStep2(props: PROPS) {
  const { addonData, billingData, totalData, planInterval, handleQuantity,nextTerm } = props;
  return (
    <div className="downgrade-body fx al-cnt fx-d-col fx-strt step-1 gap-30 step-2 scroll-small-screen">
      <div className="downgrade-body-description step-2 ">Kindly adjust the quantity of  Add-Ons you wish to have after the downgrade</div>
      <div className="downgrade-body-content step-2 fx jstfy-sp-btw gap-40">
        <div id="left" className="downgrade-left-column fx fx-d-col gap-15">
          {addonData.reduce((pre: any, addon: AddOn) => {
            if (billingData[addon.key].selected)
              pre.push(
                <div className="downgrade-addon-card fx" key={addon.key}>
                  <div className="downgrade-card-icon flex-cnt">{ADDON_ICON[addon.key]}</div>
                  <div className="downgrade-card-body fx al-cnt jstfy-sp-btw">
                    <div className="downgrade-card-content fx fx-d-col fx-strt">
                      <div className="downgrade-card-title">{addon.name}</div>
                      <div className="downgrade-card-price fx gap-5">
                        ${addon.price[planInterval]}
                        <span>
                          /{addon.key === "user" ? "user" : addon.step[planInterval]}
                          {addon.key == "user"}
                        </span>
                      </div>
                    </div>
                    <div className="downgrade-card-quntity fx fx-d-col al-fx-end fx-strt gap-8">
                      <div className="downgrade-card-current">
                        Current plan:
                        {addon.key === "branding"
                          ? billingData[addon.key].quantity
                            ? "Enabled"
                            : "Disabled"
                          : billingData[addon.key].quantity * addon.step[planInterval]}
                      </div>
                      <div className="downgrade-card-input fx al-cnt jstfy-sp-btw">
                        {addon.key === "branding" ? (
                          <Fragment>
                            <button
                              className="downgrade-card-no"
                              onClick={() => handleQuantity(addon.key, !billingData[addon.key].downgradedQuantity ? "increase" : "decrease")}
                            >
                              {!billingData[addon.key].downgradedQuantity ? "Add" : "Remove"}
                            </button>
                          </Fragment>
                        ) : (
                          <Fragment>
                            <button className={billingData[addon.key].downgradedQuantity === 0 ? "downgrade-card-decrease disable" : "downgrade-card-decrease"}  onClick={() => handleQuantity(addon.key, "decrease")}>
                              -
                            </button>
                            <div className="downgrade-card-value">
                              {billingData[addon.key].downgradedQuantity * addon.step[planInterval]}
                            </div>
                            <button className={billingData[addon.key].downgradedQuantity === billingData[addon.key].quantity ? "downgrade-card-increase disable" : "downgrade-card-increase"} onClick={() => handleQuantity(addon.key, "increase")}>
                              +
                            </button>
                          </Fragment>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              );
            return pre;
          }, [])}
        </div>
        <hr />
        <div className="downgrade-right-column fx fx-d-col jstfy-sp-btw">
          <div className="downgrade-right-header fx al-cnt">
            <p>Updated add-ons billing summary</p>
          
          </div>
          <div>
            <div className="downgrade-right-body p-relative fx fx-d-col">
              <div className="downgrade-right-table-head">
                <div className="downgrade-right-table-row responsive__border__bottom">
                  <div className="downgrade-right-table-cell">Name</div>
                  <div className="downgrade-right-table-cell">Quantity</div>
                  <div className="downgrade-right-table-cell">Total</div>
                </div>
              </div>
              <div className="downgrade-right-table-body">
                {addonData.reduce((pre: any, addon: AddOn) => {
                  if (billingData[addon.key].selected)
                    pre.push(
                      <div className="downgrade-right-table-row" key={addon.key}>
                        <div className="downgrade-right-table-cell downgrade__name__responsive">
                          {addon.name}
                          <div className="downgrade-card-current hide__current__plan">
                        Current plan:
                        {addon.key === "branding"
                          ? billingData[addon.key].quantity
                            ? "Enabled"
                            : "Disabled"
                          : billingData[addon.key].quantity * addon.step[planInterval]}
                      </div>
                          </div>
                        <div className="downgrade-right-table-cell qunatity__responsive__downgrade">
                          {addon.step[planInterval] * billingData[addon.key].downgradedQuantity}
                        </div>
                        <div className="downgrade-card-quntity fx fx-d-col al-fx-end fx-strt gap-8 downgrade-card-quntity__responsive">
                      <div className="downgrade-card-input fx al-cnt jstfy-sp-btw">
                        {addon.key === "branding" ? (
                          <Fragment>
                            <button
                              className="downgrade-card-no"
                              onClick={() => handleQuantity(addon.key, !billingData[addon.key].downgradedQuantity ? "increase" : "decrease")}
                            >
                              {!billingData[addon.key].downgradedQuantity ? "Add" : "Remove"}
                            </button>
                          </Fragment>
                        ) : (
                          <Fragment>
                            <button className="downgrade-card-decrease" onClick={() => handleQuantity(addon.key, "decrease")}>
                              -
                            </button>
                            <div className="downgrade-card-value">
                              {billingData[addon.key].downgradedQuantity * addon.step[planInterval]}
                            </div>
                            <button className="downgrade-card-increase" onClick={() => handleQuantity(addon.key, "increase")}>
                              +
                            </button>
                          </Fragment>
                        )}
                      </div>
                    </div>
                        <div className="downgrade-right-table-cell responsive__bold">
                          ${addon.price[planInterval] * billingData[addon.key].downgradedQuantity}
                        </div>
                      </div>
                    );
                  return pre;
                }, [])}
              </div>
              <div className="blurbox-downgrade"></div>
            </div>
            <div className="downgrade-right-footer fx fx-d-col p-relative footer__responsive__container">
              <div className="downgrade-right-amount">
                <div className="downgrade-right-table-row footer__responsive">
                  <div className="downgrade-right-table-cell">Current amount</div>
                  <div className="downgrade-right-table-cell width__mobile">${totalData.currentTotal}</div>
                </div>
                <div className="downgrade-right-table-row footer__responsive">
                  <div className="downgrade-right-table-cell">Downgraded amount</div>
                  <div className="downgrade-right-table-cell red minus width__mobile">${totalData.currentTotal - totalData.updatedTotal}</div>
                </div>
                <div className="downgrade-right-table-row footer__responsive">
                  <div className="downgrade-right-table-cell">Updated amount</div>
                  <div className="downgrade-right-table-cell width__mobile">${totalData.updatedTotal}</div>
                </div>
              </div>
              <div className="downgrade-right-total">
                <div className="downgrade-right-table-row footer__responsive">
                  <div className="downgrade-right-table-cell grand-total fx fx-strt al-cnt">
                    <p>
                      <span>Grand Total </span>(All  Add-Ons)
                    </p>
                    <div className="invoice-tp">
                      <svg width="13" height="13" viewBox="0 0 13 13" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <g clipPath="url(#clip0_11357_16482)">
                          <path
                            d="M6.5 0C5.21442 0 3.95772 0.381218 2.8888 1.09545C1.81988 1.80968 0.986755 2.82484 0.494786 4.01256C0.00281636 5.20028 -0.125905 6.50721 0.124899 7.76809C0.375703 9.02896 0.994767 10.1872 1.90381 11.0962C2.81285 12.0052 3.97104 12.6243 5.23192 12.8751C6.49279 13.1259 7.79973 12.9972 8.98744 12.5052C10.1752 12.0132 11.1903 11.1801 11.9046 10.1112C12.6188 9.04229 13 7.78558 13 6.5C12.9981 4.77667 12.3127 3.12445 11.0941 1.90586C9.87556 0.687282 8.22334 0.00186392 6.5 0V0ZM6.5 11.375C5.53582 11.375 4.59329 11.0891 3.7916 10.5534C2.98991 10.0177 2.36507 9.25637 1.99609 8.36558C1.62711 7.47479 1.53057 6.49459 1.71867 5.54893C1.90678 4.60328 2.37108 3.73464 3.05286 3.05285C3.73464 2.37107 4.60328 1.90677 5.54894 1.71867C6.49459 1.53057 7.47479 1.62711 8.36558 1.99609C9.25637 2.36506 10.0177 2.98991 10.5534 3.7916C11.0891 4.59329 11.375 5.53582 11.375 6.5C11.3734 7.79245 10.8593 9.03151 9.94541 9.94541C9.03151 10.8593 7.79245 11.3734 6.5 11.375Z"
                            fill="black"
                            fillOpacity="0.5"
                          ></path>
                          <path
                            d="M6.25366 5.17018H6.09116C5.87828 5.16495 5.67111 5.23943 5.51029 5.37901C5.34946 5.5186 5.24657 5.71322 5.22179 5.92472C5.20762 6.13631 5.27673 6.34505 5.41437 6.50638C5.55201 6.6677 5.74727 6.76882 5.95845 6.78814V9.30689C5.95845 9.52238 6.04406 9.72904 6.19643 9.88141C6.3488 10.0338 6.55547 10.1194 6.77095 10.1194C6.98644 10.1194 7.19311 10.0338 7.34548 9.88141C7.49785 9.72904 7.58345 9.52238 7.58345 9.30689V6.49997C7.58345 6.14729 7.44335 5.80905 7.19397 5.55967C6.94458 5.31028 6.60635 5.17018 6.25366 5.17018Z"
                            fill="black"
                            fillOpacity="0.5"
                          ></path>
                          <path
                            d="M6.40802 4.58556C6.59185 4.58556 6.77156 4.53105 6.92442 4.42891C7.07727 4.32678 7.19641 4.18161 7.26676 4.01177C7.33711 3.84192 7.35552 3.65503 7.31966 3.47473C7.28379 3.29442 7.19526 3.1288 7.06527 2.99881C6.93528 2.86881 6.76966 2.78029 6.58935 2.74442C6.40905 2.70856 6.22216 2.72697 6.05231 2.79732C5.88247 2.86767 5.7373 2.9868 5.63516 3.13966C5.53303 3.29252 5.47852 3.47223 5.47852 3.65606C5.47844 3.77815 5.50244 3.89905 5.54912 4.01185C5.59581 4.12466 5.66428 4.22715 5.7506 4.31348C5.83693 4.3998 5.93942 4.46827 6.05223 4.51495C6.16503 4.56164 6.28593 4.58563 6.40802 4.58556Z"
                            fill="black"
                            fillOpacity="0.5"
                          ></path>
                        </g>
                        <defs>
                          <clipPath id="clip0_11357_16482">
                            <rect width="13" height="13" fill="white"></rect>
                          </clipPath>
                        </defs>
                      </svg>
                      <div className="confirm-total-tooltip">
                        Full charges for these  Add-Ons will be added to your next bill on <strong>{moment(nextTerm).format('MMMM Do YYYY') }</strong>.
                      </div>
                    </div>
                  </div>
                  <div className="downgrade-right-table-cell red width__mobile ft-500">${totalData.allTotal}</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default DowngradeStep2;
