import {
  Fragment,
  Dispatch,
  SetStateAction,
  useState,
  useEffect,
  useMemo,
} from "react";
import { useDispatch } from "react-redux";
import { userSlice } from "../../../redux/User";
import { axiosPost } from "../../../services/axiosService";
import "../../../styles/downgrade.css";
import environment from "../../../constants/Environment";
import DowngradeStep1 from "./DowngradeStep1";
import DowngradeStep2 from "./DowngradeStep2";
import DowngradeStep3 from "./DowngradeStep3";
import { AddOn } from "../manageAddons";
interface PROPS {
  addonData: AddOn[];
  nextTerm: any;
  usageData: any;
  downgradeData: any[];
  previousAddons: any[];
  planInterval: "monthly" | "yearly";
  handleModalClose: () => void;
  handleReload: Dispatch<SetStateAction<boolean>>;
}
export interface Billing {
  selected: boolean;
  quantity: number;
  downgradedQuantity: number;
}

export interface Total {
  currentTotal: number;
  updatedTotal: number;
  allTotal: number;
}

const STEPS =
  window.innerWidth < 990
    ? ["Select Add-ons", "Adjust Add-ons", "Why Downgrade"]
    : ([
        "Select your  Add-Ons",
        "Adjust  Add-Ons",
        "Reason of Downgrade",
      ] as const);

const totalSteps = STEPS.length;
// type StepNnumber = (Array.from({length: STEPS.length}, (_, i) => i + 1)).join(" | ");

function Downgrade(props: PROPS) {
  const {
    addonData,
    usageData,
    downgradeData,
    previousAddons,
    planInterval,
    handleModalClose,
    handleReload,
    nextTerm,
  } = props;
  const dispatch = useDispatch();
  const isAI = environment().isAI;
  const [stepNumber, setStepNumber] = useState<number>(1);
  const [billingData, setBillingData] = useState<Record<string, Billing>>({});
  const [downgradeReason, setDowngradeReason] = useState<string>("");
  const [loading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    const billingData: Record<string, Billing> = {};
    addonData.forEach((addon: AddOn) => {
      const data: any = isAI
        ? usageData.ai_addons[addon.key] ?? {}
        : usageData.omniaddons[addon.key] ?? {};
      const quantity: number =
        addon.key === "branding" ? Number(data) : data.quantity;

      const downgradedQuantity: number = downgradeData.reduce(
        (total: number, downgrade: any) =>
          total +
          downgrade.omniaddons.reduce(
            (subtotal: number, item: any) =>
              item.addon_code === addon.addon_code[planInterval]
                ? subtotal + item.quantity
                : subtotal,
            0,
          ),
        0,
      );

      billingData[addon.key] = {
        selected: false,
        quantity: Math.max(quantity - downgradedQuantity, 0),
        downgradedQuantity: Math.max(quantity - downgradedQuantity, 0),
      };
    });

    setBillingData(billingData);
  }, []);

  const totalData: Total = useMemo(
    () =>
      addonData.reduce(
        (pre: Total, addon: AddOn) => {
          const { key, price } = addon;
          const { selected, quantity, downgradedQuantity }: Billing =
            billingData[key] ?? {};
          return {
            currentTotal:
              pre.currentTotal +
              (selected ? quantity * price[planInterval] : 0),
            updatedTotal:
              pre.updatedTotal +
              (selected ? downgradedQuantity * price[planInterval] : 0),
            allTotal: pre.allTotal + downgradedQuantity * price[planInterval],
          };
        },
        { currentTotal: 0, updatedTotal: 0, allTotal: 0 },
      ),
    [billingData],
  );

  const enableNext: boolean = useMemo(() => {
    if (stepNumber === 1)
      return Object.keys(billingData).some(
        (key: string) => billingData[key].quantity && billingData[key].selected,
      );
    if (stepNumber === 2)
      return Object.keys(billingData).some(
        (key: string) =>
          billingData[key].downgradedQuantity < billingData[key].quantity,
      );
    return false;
  }, [stepNumber, billingData]);

  const handleAddonSelect = (addonKey: string) => {
    setBillingData({
      ...billingData,
      [addonKey]: {
        ...billingData[addonKey],
        selected: !billingData[addonKey].selected,
      },
    });
  };

  const handleQuantity = (
    addonKey: string,
    action: "increase" | "decrease",
  ) => {
    const quantity: number = action === "increase" ? 1 : -1;
    const data: Billing = billingData[addonKey];

    setBillingData({
      ...billingData,
      [addonKey]: {
        ...data,
        downgradedQuantity: Math.min(
          Math.max(data.downgradedQuantity + quantity, 0),
          data.quantity,
        ),
      },
    });
  };

  const handleStepChange = (direction: "next" | "back") => {
    if (direction === "next")
      setStepNumber((pre) => Math.min(++pre, totalSteps));
    if (direction === "back") setStepNumber((pre) => Math.max(--pre, 1));
  };

  const applyDowngrade = async () => {
    try {
      if (!downgradeReason) throw Error("Enter your reason for downgrade");
      setLoading(true);
      const addonsData: any[] = [];
      const downgradeData: any[] = [];

      addonData.forEach((addon: AddOn) => {
        const { selected, quantity, downgradedQuantity } =
          billingData[addon.key];
        const { addon_code, price } = addon;

        if (downgradedQuantity)
          addonsData.push({
            addon_code: addon_code[planInterval],
            quantity: downgradedQuantity,
            price: price[planInterval],
          });

        if (downgradedQuantity < quantity)
          downgradeData.push({
            addon_code: addon_code[planInterval],
            quantity: quantity - downgradedQuantity,
            price: price[planInterval],
          });
      });
      previousAddons?.forEach((data) => {
        downgradeData.push({
          addon_code: data.addon_code,
          quantity: data.quantity,
          price: data.price,
        });
      });

      console.log(downgradeData);

      if (!downgradeReason) throw Error("No addon selected for downgrade");
      await axiosPost(
        `/payments/${isAI ? "aidowngradeAddon" : "downgradeAddon"}`,
        {
          downgradeReason: downgradeReason,
          addons: addonsData,
          downgrades: downgradeData,
        },
      );
      handleModalClose();
      dispatch(
        userSlice.actions.setInfoModal({
          showModal: true,
          modalType: "success",
          modalTitle: "Add-ons downgraded successfully",
          modalMessage:
            "The downgrade changes made will be reflected in your account from next billing.",
          modalButtonText: "Go To Dashboard",
        }),
      );
      handleReload((pre) => !pre);
    } catch (err: any) {
      console.log(err);
      dispatch(
        userSlice.actions.setInfoModal({
          showModal: true,
          modalType: "error",
          modalTitle: "OOPS!",
          modalMessage: err.message,
        }),
      );
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="downgrade-main flex-cnt wd-100" onClick={handleModalClose}>
      <div className="outer-downgrade">
        <div
          className="downgrade-container p-relative"
          onClick={(e) => e.stopPropagation()}
        >
          <div className="downgrade-close-btn" onClick={handleModalClose}>
            <svg
              width="26"
              height="26"
              viewBox="0 0 26 26"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <circle cx="13" cy="13" r="13" fill="white" />
              <g clipPath="url(#clip0_11357_16623)">
                <path
                  d="M18 9.00714L16.9929 8L13 11.9929L9.00714 8L8 9.00714L11.9929 13L8 16.9929L9.00714 18L13 14.0071L16.9929 18L18 16.9929L14.0071 13L18 9.00714Z"
                  fill="#6D6D6D"
                />
              </g>
              <defs>
                <clipPath id="clip0_11357_16623">
                  <rect
                    width="10"
                    height="10"
                    fill="white"
                    transform="translate(8 8)"
                  />
                </clipPath>
              </defs>
            </svg>
          </div>
          <div className="downgrade-header">
            <div className="downgrade-description">
              <div className="downgrade-title">Downgrade Add-Ons</div>
              <div className="downgrade-subtitle mt-10">
                Any changes made to downgrade an Add-on will not take effect
                until your next billing cycle
              </div>
            </div>
            <div className="downgrade-progress flex-cnt">
              {STEPS.map((step, stepIndex: number) => (
                <Fragment key={stepIndex}>
                  <div className="downgrade-progress-step fx al-cnt">
                    {stepIndex < stepNumber ? (
                      <div className="downgrade-progress-icon p-relative flex-cnt">
                        <span>{stepIndex + 1}</span>
                        <svg
                          width="24"
                          height="24"
                          viewBox="0 0 24 24"
                          fill="none"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <circle
                            cx="12"
                            cy="12"
                            r="11.5"
                            fill="#FB5F66"
                            stroke="#FB5F66"
                          />
                          <circle cx="12" cy="12" r="10" fill="#FF8287" />
                        </svg>
                      </div>
                    ) : (
                      <div className="downgrade-progress-icon p-relative flex-cnt">
                        <span className="inactive">{stepIndex + 1}</span>
                        <svg
                          width="24"
                          height="24"
                          viewBox="0 0 24 24"
                          fill="none"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <circle
                            cx="12"
                            cy="12"
                            r="11.5"
                            stroke="#999999"
                            strokeLinejoin="round"
                            strokeDasharray="3 3"
                          />
                          <circle cx="12" cy="12" r="9" fill="#F1F1F1" />
                        </svg>
                      </div>
                    )}
                    <p
                      style={{
                        color: `${stepIndex < stepNumber ? "#444444" : "#999999"}`,
                      }}
                    >
                      {step}
                    </p>
                  </div>
                  {stepIndex + 1 < STEPS.length && (
                    <svg
                      width="6"
                      height="10"
                      viewBox="0 0 6 10"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M0.532695 0.627237C0.532841 0.792941 0.598784 0.951806 0.716028 1.0689L3.9127 4.26557C4.00945 4.3623 4.08621 4.47715 4.13857 4.60355C4.19094 4.72994 4.21789 4.86542 4.21789 5.00224C4.21789 5.13905 4.19094 5.27453 4.13857 5.40093C4.08621 5.52733 4.00945 5.64217 3.9127 5.7389L0.720195 8.9314C0.606346 9.04928 0.543349 9.20716 0.544774 9.37103C0.546197 9.5349 0.611928 9.69166 0.727808 9.80754C0.843688 9.92342 1.00045 9.98915 1.16432 9.99057C1.32819 9.992 1.48607 9.929 1.60394 9.81515L4.79645 6.62515C5.22571 6.19504 5.4668 5.6122 5.4668 5.00453C5.4668 4.39686 5.22571 3.81401 4.79645 3.3839L1.59978 0.185153C1.51237 0.0976884 1.40098 0.0381173 1.2797 0.0139781C1.15843 -0.0101611 1.03272 0.00221645 0.918479 0.0495446C0.804241 0.0968727 0.706608 0.177024 0.637936 0.279857C0.569264 0.38269 0.532639 0.503582 0.532695 0.627237Z"
                        fill="#999999"
                      />
                    </svg>
                  )}
                </Fragment>
              ))}
            </div>
          </div>
          {stepNumber === 1 && (
            <DowngradeStep1
              addonData={addonData}
              billingData={billingData}
              handleAddonSelect={handleAddonSelect}
            />
          )}
          {stepNumber === 2 && (
            <DowngradeStep2
              addonData={addonData}
              billingData={billingData}
              totalData={totalData}
              planInterval={planInterval}
              handleQuantity={handleQuantity}
              nextTerm={nextTerm}
            />
          )}
          {stepNumber === 3 && (
            <DowngradeStep3
              downgradeReason={downgradeReason}
              handleDowngradeReason={setDowngradeReason}
            />
          )}
          <div className="downgrade-footer flex-cnt gap-20">
            {stepNumber !== 1 && (
              <button
                className="downgrade-back-btn flex-cnt"
                onClick={() => handleStepChange("back")}
              >
                Back
              </button>
            )}
            {stepNumber !== totalSteps ? (
              <button
                className={
                  enableNext
                    ? "downgrade-next-btn flex-cnt"
                    : "downgrade-next-btn flex-cnt disable"
                }
                onClick={() => enableNext && handleStepChange("next")}
              >
                Next
              </button>
            ) : (
              <button
                className="downgrade-submit-btn flex-cnt"
                onClick={applyDowngrade}
              >
                {loading ? "Submit..." : "Submit"}
              </button>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Downgrade;
